@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;400;500;600;700&display=swap);
.nav-bar{width:15%;height:100%;background-color:#191D2B;position:fixed;border-right:1px solid #2e344e;border-left:none;display:flex;flex-direction:column;justify-content:space-between;align-items:center}.nav-bar__avatar-container{width:100%;text-align:center;padding:1rem 0;border-bottom:1px solid #2e344e}.nav-bar__avatar{width:85%;border-radius:50%;border:5px solid #2e344e}.nav-bar .footer{border-top:inherit}.nav-bar__navigation{display:grid;width:100%;text-align:center}.nav-bar__navigation-item{padding:.5rem 0;text-decoration:none;color:inherit;position:relative}.nav-bar__navigation-item:before{content:"";position:absolute;top:0;left:0;width:.4rem;height:100%;background-color:#0381ff33;transform-origin:bottom;transform:scale(0);overflow-x:hidden;transition:transform 0.6s,0.2s width 0.5s cubic-bezier(1, -0.16, 0.6, 1.35)}.nav-bar__navigation-item:hover:before{width:100%;transform:scale(1)}.nav-bar .footer{padding:.5rem 0;border-top:1px solid #2e344e;width:100%;text-align:center}.nav-bar .active{background-color:#037FFF}

.aws-btn{text-transform:uppercase;--button-default-border-radius: 10px !important;position:relative}.awssld{width:48%;--loader-bar-color: $blue-color;--organic-arrow-color: #a4acc4;--slider-transition-duration: .3s;--organic-arrow-thickness: 7px;--organic-arrow-height: 25px}.awssld__bullets{bottom:-25px}.awssld__bullets button{width:10px;height:10px;background:#a4acc4}.awssld__bullets .awssld__bullets--active{background:#037FFF}

.about-page__info{width:48%}.about-page__info-text{margin-bottom:1rem}.about-page__info-header{line-height:1;margin-bottom:1rem}.about-page__main-info{display:flex;justify-content:space-between;margin-bottom:5rem}.about-page__header-inner{color:#037FFF}.about-page__table{display:grid;grid-template-columns:.6fr 1fr;margin-bottom:1rem}.about-page__table-item{margin-bottom:.5rem}.about-page__btn-container{text-align:center}.about-page__btn{text-transform:uppercase;text-decoration:none;color:#a4acc4;background:linear-gradient(180deg, #037fff 0%, #0056d8 50%);padding:1rem 1rem;border-radius:10px;position:relative}.about-page__btn:hover{background:linear-gradient(180deg, #0056d8 50%, #037fff 100%)}.about-page__btn:after{position:absolute;content:"";width:0;height:.3rem;left:2px;bottom:0;background-color:#a4acc4;border-bottom-left-radius:10px;border-bottom-right-radius:10px;transition:all 0.4s ease-in-out}.about-page__btn:hover:after{width:99%}.about-page__skills-wrapper{display:grid;grid-template-columns:.1fr 1fr}.about-page__skills-wrapper:not(:last-child){margin-bottom:1rem}.about-page__skills-item{font-size:3rem;color:#037FFF}.about-page__skills-list{margin:1rem 0 0 1.3rem}.about-page__list-item{list-style:circle}

.title{text-transform:uppercase;font-size:2rem;color:#fff;position:relative;margin-bottom:6rem}.title:before{content:"";bottom:-20%;position:absolute;left:0;background-color:#0381ff38;width:6rem;height:.4rem;border-radius:50px}.title:after{content:"";bottom:-20%;position:absolute;left:0;background-color:#037FFF;width:3rem;height:.4rem;border-radius:50px}.title .title-reflection{color:#037FFF;font-size:4rem;position:absolute;top:55px;left:4%;opacity:.2}.main-page{text-align:center}.main-page__header{color:#fff;font-size:3.2rem;max-width:795px;margin:0 auto;padding-bottom:1rem}.main-page__header-inner{color:#037FFF}.main-page__icon-holder{font-size:4rem;color:#a4acc4;transition:.5s}.main-page__icon-holder:hover{color:#037FFF}.main-page__wrapper{position:absolute;top:40%;margin:0 10rem}.main-page__text,.main-page__header{-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);border-radius:20%}

*{margin:0;padding:0;box-sizing:border-box}body{color:#a4acc4;font-family:'Comfortaa', cursive;font-size:1.2rem;line-height:2rem}body::-webkit-scrollbar{width:8px}body::-webkit-scrollbar-thumb{background-color:rgba(0,86,216,0.596078);border-radius:50px}body::-webkit-scrollbar-track{background-color:#777}.main-content{width:85%;margin-left:15%;min-height:100vh;background:linear-gradient(90deg, #10121b 0%, #001c46 100%)}.blue{color:#037FFF}.container{max-width:1170px;padding:2rem 1rem;margin:0 auto}.tsparticles-canvas-el{min-height:100vh}

.achievements-page__list{display:grid;grid-template-columns:.05fr .07fr 1fr}.achievements-page__list:not(:last-child){margin-bottom:1rem}.achievements-page__list-item{font-size:2rem}.achievements-page__list-year,.achievements-page__list-item{text-align:center}

.contact-page__item-wrapper{display:flex}.contact-page__item{width:-webkit-max-content;width:-moz-max-content;width:max-content;background-color:#191D2B;padding:4rem}.contact-page__item-img{border:1px solid #a4acc4;line-height:63px;padding:0 14px;transition-duration:1s}.contact-page__item-img:hover{color:#037FFF;border:1px solid #037FFF}.contact-page__item-info{margin-left:1rem}.contact-page__item-wrapper{width:250px}.contact-page__icon{margin-top:2rem}.contact-page__icon-holder{font-size:3rem;color:#a4acc4;transition-duration:1s}.contact-page__icon-holder:hover{color:#037FFF}

